body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 16px;
  margin: 0;
}

form {
  background-color: lightcoral;
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
  padding-top: 15px;
  padding-bottom: 15px;
}

input {
  width: 250px;
  height: 25px;
  margin-right: 5px;
  border: dotted coral;
}

input:focus {
  outline: none;
}

button {
  background-color: white;
  border: dotted coral;
  width: 100px;
  color: coral;
  cursor: pointer;
}

.load-more {
  display: none;
}

.gallery {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
  margin: 10px;
}

.photo-card {
  flex-basis: calc((100% - 45px) / 4);
  width: 300px;
  height: 250px;
}

img {
  width: 100%;
  height: 80%;
}

.info {
  display: flex;
}

.lmstyle {
  display: flex;
  justify-content: center;
  margin-top: 30px;
  margin-bottom: 20px;
  margin-left: 10px;
  margin-right: 10px;
  width: 100%;
  background-color: lightcoral;
  color: white;
  font-style: italic;
  font-size: 15px;
  border: dotted white;
}

.lmstyle:focus,
.lmstyle:active,
.lmstyle:hover {
  background-color: white;
  color: lightcoral;
}
